// Elements that apply to all modules
.emphasis {
  color: $contrastColor;
  font-size: 150%;
}

.detach {
  background-color: $mainColor;
  color: $colorCardText;
  font-size: 120%;
}

// info card on different pages
.infoCard {
  margin-top: 5%;
  margin-bottom: 2%;
  box-shadow: 0 4px 8px 0#2e303f;
  background-color: $mainColor;
  border-radius: 5px;
  opacity: 0.8;
  width: 250px;
  display: flex;
  justify-content: space-between;
}


svg {
  width: 60px;
}

.simpleButtonStyle {
  background-color: $contrastColor;
  color: $mainBackColor;
  border-radius: 20px;
  width: 85px;
  height: 65px;
  margin-left: 20px;
}
